body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: left;
  position: relative;
  margin: 0 auto; }
  .App .Checked {
    background-color: #26f17b; }
  .App .Checking {
    background-color: #e69318; }
  .App .InProgress {
    background-color: #e3f126; }
  .App .ToDo {
    background-color: #bbbaba; }
  .App .failed {
    background-color: #f12626; }
  .App .dismissed {
    opacity: 0.4; }
  .App [tooltip]:after {
    /* needed - do not touch */
    content: attr(tooltip);
    position: absolute;
    opacity: 0;
    z-index: 3;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    /* customizable */
    min-width: 150%;
    -webkit-transition: all 0.15s ease;
    transition: all 0.15s ease;
    padding: 10px;
    color: #333;
    border: solid 1px black;
    border-radius: 10px;
    box-shadow: 2px 2px 1px silver; }
  .App [tooltip]:hover:after {
    /* needed - do not touch */
    opacity: 1;
    /* customizable */
    background: #ffffff;
    margin-top: 10px; }
  .App [tooltip]:not([tooltip-persistent]):after {
    pointer-events: none; }
  @media screen and (min-width: 1200px) {
    .App * {
      font-size: 1.3rem; }
    .App .wrapper {
      margin: auto;
      padding-top: 1%;
      min-width: 1200px;
      max-width: 95%; }
    .App .mentor-table {
      margin: auto;
      padding-top: 1%;
      text-align: center; }
    .App .mentorTitle,
    .App .mentorName {
      min-width: 300px;
      font-weight: bold;
      font-size: 1.3em; }
    .App .description-table {
      margin: auto;
      padding-top: 2%; }
      .App .description-table .description-table__cell {
        min-width: 180px;
        padding-left: 10px; }
    .App .css-10nd86i {
      padding-top: 2%;
      margin: auto;
      width: 60%; }
    .App .cell {
      min-width: 100px;
      text-align: center; } }
  @media screen and (max-width: 1199px) and (min-width: 600px) {
    .App * {
      font-size: 0.9rem; }
    .App .mentor-table {
      padding-top: 1%;
      margin: auto;
      text-align: center; }
    .App .mentorTitle,
    .App .mentorName {
      min-width: 300px;
      text-align: center;
      font-weight: bold;
      font-size: 1.3em; }
    .App .wrapper {
      padding-top: 1%;
      margin: auto;
      min-width: 600px;
      max-width: 90%;
      overflow-x: auto;
      table-layout: fixed;
      top: 140px; }
    .App .description-table {
      padding-top: 2%;
      margin: auto; }
      .App .description-table .description-table__cell {
        min-width: 150px;
        padding-left: 10px; }
    .App .css-10nd86i {
      padding-top: 3%;
      margin: auto;
      width: 80%; }
    .App .cell {
      min-width: 70px;
      text-align: center; } }
  @media screen and (max-width: 599px) and (min-width: 300px) {
    .App .mentor-table {
      margin: auto;
      margin-top: 5%;
      max-width: 300px;
      text-align: center; }
    .App .wrapper {
      padding-top: 5%;
      min-width: 300px;
      max-width: 90%;
      overflow-x: auto;
      margin: auto;
      table-layout: fixed; }
    .App .mentorTitle,
    .App .mentorName {
      min-width: 40%;
      font-weight: bold;
      font-size: 1.3em; }
    .App .css-10nd86i {
      padding-top: 5%;
      margin: auto;
      width: 80%; }
    .App .description-table {
      padding-top: 5%;
      margin: auto;
      position: relative; }
      .App .description-table .description-table__cell {
        min-width: 100px;
        padding-left: 10px; }
    .App .cell {
      min-width: 62px;
      text-align: center;
      word-wrap: break-word; }
    .App * {
      font-size: 0.8rem; } }
  .App .statistics,
  .App .check_time {
    padding-left: 0.5%;
    padding-right: 0.5%;
    background-color: #db59fc; }
  .App .studentName {
    background-color: #59b3fc;
    position: relative; }
  .App td {
    min-height: 25px; }
  .App .link {
    display: block;
    min-width: 90px;
    min-height: 17px;
    padding-left: 4px;
    padding-right: 4px;
    text-decoration: none;
    color: black; }

